@import "../../styles/index.scss";

.Loader {
    background-color: $mask-color;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 500;

    .spinner {
        position: absolute;
        height: 60px;
        width: 60px;
        border: 3px solid transparent;
        border-top-color: #A04668;
        top: 35%;
        left: 40%;
        margin: -30px;
        border-radius: 50%;
        animation: spin 2s linear infinite;

        &:before,
        &:after {
            content: '';
            position: absolute;
            border: 3px solid transparent;
            border-radius: 50%;
        }

        &:before {
            border-top-color: #254E70;
            top: -12px;
            left: -12px;
            right: -12px;
            bottom: -12px;
            animation: spin 3s linear infinite;
        }

        &:after {
            border-top-color: #8ccbe0;
            top: 6px;
            left: 6px;
            right: 6px;
            bottom: 6px;
            animation: spin 4s linear infinite;
        }

    }

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }
}